.addTodoForm {
    max-width: 500px;
    margin: 0 auto;
    min-height: 400px;
    padding: 40px;
    text-align: center;
    border: 1px solid #ffffff;
    border-radius: 5px;
    background-color: #080a081a;
    -webkit-box-shadow: 0px 0px 9px 2px #000000;
    box-shadow: 0px 0px 9px 2px #000000;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.formFields{
    width: 100%;
}

.addTodoForm input,
.addTodoForm textarea{
    display: block;
    max-width: 80%;
    width: 100%;
    min-height: 40px;
    margin: 20px auto;
    padding: 5px 10px;
    display: block;
    border: none;
    border-radius: 5px;
}

.addTodoForm textarea {
    min-height: 100px;
    resize: none;
}

.formControls {
    min-height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
}


